.dev-info {
  box-sizing: border-box;

  &:hover {
    .dev-info__logo {
      fill: $basicGreen;
    }
  }

  &:active {
    opacity: 0.3;
  }
}

.dev-info__title {
  font-family: $Arial;
  font-size: 16px;
  line-height: 18px;
  color: $deepGraphite;
  text-decoration: none;
}

.dev-info__link {
  width: 37px;
  text-decoration: none;
}

.dev-info__logo {
  fill: $darkGrey;
}
