.program-selection {
  padding-top: 27px;
  padding-bottom: 32px;
}

.program-selection__call {
  display: block;
  width: 74vw;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 18px;
  margin-top: 37px;

  @media (min-width: $tabletWidth) {
    font-size: 20px;
    line-height: 30px;
    padding-left: 40px;
    width: 324px;
    margin-top: 54px;
  }

  @media (min-width: $desktopWidth) {
    padding-left: 0;
  }
}

.program-selection__form {
  margin-top: 39px;

  @media (min-width: $tabletWidth) {
    margin-top: 66px;
  }
}

.program-selection__field {
  box-sizing: border-box;
  display: grid;
  gap: 13px;
  border: none;
  padding: 0;
  padding-bottom: 24px;
  position: relative;
  margin: 0;

  @media (min-width: $tabletWidth) {
    padding-left: 40px;
    padding-right: 40px;
  }

  @media (min-width: $desktopWidth) {
    padding-left: 0;
    padding-right: 0;
    padding-bottom: 0;
  }

  &::after {
    @include bottom-border;
    bottom: 0;
  }

  &--reason {
    margin-top: 26px;

    @media (min-width: $tabletWidth) {
      margin-top: 71px;
      padding-top: 30px;
      padding-left: 0;
      padding-right: 0;
      max-width: 628px;
      border: 1px solid $lightGrey;
      margin-left: 40px;
    }

    @media (min-width: $desktopWidth) {
      margin-left: 0;
      margin-top: 0;
    }
  }

  &--contacts {
    margin-top: 26px;

    @media (min-width: $tabletWidth) {
      margin-top: 71px;
      padding-bottom: 0;
    }

    &::after {
      display: none;
    }
  }

  &--extra {
    margin-top: 26px;

    @media (min-width: $tabletWidth) {
      margin-top: 65px;
    }

    &::after {
      display: none;
    }
  }
}

.program-selection__text-label {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  margin-top: 13px;
  color: $deepGraphite;

  &:first-child {
    margin-top: 0;
  }

  @media (min-width: $tabletWidth) {
    font-size: 20px;
    line-height: 30px;
    margin-top: 20px;
  }
}

.program-selection__legend {
  font-size: 24px;
  line-height: 26px;

  @media (min-width: $tabletWidth) {
    font-size: 36px;
    line-height: 36px;
  }

  &--comment {
    @media (min-width: $tabletWidth) {
      display: block;
      padding-left: 40px;
      margin-top: 65px;
    }

    @media (min-width: $desktopWidth) {
      padding-left: 0;
    }
  }
}

.program-selection__checkbox-label,
.program-selection__reason-label {
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  margin-top: 25px;
  padding-left: 36px;

  &:first-child {
    margin-top: 0;
  }

  @media (min-width: $tabletWidth) {
    font-size: 20px;
    line-height: 30px;
    padding-left: 60px;
  }
}

.program-selection__checkbox-label {
  @media (min-width: $tabletWidth) {
    margin: 0;
  }
}

.program-selection__checkbox,
.program-selection__radio-input {
  display: none;
}

.program-selection__text-input {
  flex-grow: 1;
  font-family: $Oswald;
  text-transform: uppercase;
  box-sizing: border-box;
  height: 45px;
  margin-left: 10px;
  border: 1px solid $lightGrey;
  border-radius: 0;
  padding-left: 15px;
  color: $deepGraphite;

  &--email {
    background-image: url("../img/icon-mail.svg");
    background-repeat: no-repeat;
    background-size: 20px 14px;
    background-position: right 15px bottom 50%;
  }

  &--tel {
    background-image: url("../img/icon-phone.svg");
    background-repeat: no-repeat;
    background-size: 20px 20px;
    background-position: right 15px bottom 50%;
  }

  @media (min-width: $tabletWidth) {
    height: 52px;
  }
}

.program-selection__textarea {
  font-family: $Oswald;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 18px;
  padding: 15px 13px 12px;
  box-sizing: border-box;
  width: 100%;
  border: 1px solid $lightGrey;
  border-radius: 0;
  resize: none;
  height: 150px;
  margin-top: 34px;

  @media (min-width: $tabletWidth) {
    font-size: 20px;
    line-height: 30px;
    max-width: 630px;
    margin-top: 76px;
    height: 174px;
  }

  @media (min-width: $desktopWidth) {
    max-width: 100%;
  }
}

.program-selection__required-info {
  display: block;
  font-family: $Arial;
  text-align: center;
  margin-top: 21px;
  line-height: 16px;

  @media (min-width: $tabletWidth) {
    margin-left: 61px;
    margin-top: 0;
    font-size: 16px;
  }

  @media (min-width: $desktopWidth) {
    margin-left: 81px;
  }
}

.program-selection__submit {
  font-family: $Oswald;
  margin-top: 17px;
  width: 100%;

  @media (min-width: $tabletWidth) {
    width: 282px;
    font-size: 20px;
    line-height: 26px;
    margin-top: 0;
  }

  @media (min-width: $desktopWidth) {
    width: 568px;
  }
}

.program-selection__extra-wrapper {
  display: grid;
  margin-top: 39px;

  @media (min-width: $tabletWidth) {
    grid-template-columns: 221px 221px;
    gap: 32px 123px;
    margin-top: 57px;
  }

  @media (min-width: $desktopWidth) {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}

.program-selection__contacts-wrapper {
  margin-top: 32px;

  @media (min-width: $tabletWidth) {
    margin-top: 76px;
  }

  @media (min-width: $desktopWidth) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 78px;
  }
}

.program-selection__main-wrapper {
  @media (min-width: $desktopWidth) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 80px;
  }
}

.program-selection__submit-wrapper {
  @media (min-width: $tabletWidth) {
    padding-left: 40px;
    padding-right: 40px;
    display: flex;
    align-items: center;
    margin-top: 30px;
  }

  @media (min-width: $desktopWidth) {
    padding: 0;
    margin-top: 61px;
  }
}

.program-selection__contacts-wrapper .program-selection__text-label {
  @media (min-width: $desktopWidth) {
    margin-top: 0;
  }
}
