.product-card {
  padding-bottom: 26px;
  padding-top: 19px;

  @media (min-width: $tabletWidth) {
    padding-top: 161px;
    padding-bottom: 40px;
    background-color: $shadowWhite;
    padding-left: 36px;
    padding-right: 36px;
    position: relative;
  }

  @media (min-width: $desktopWidth) {
    padding-left: 38px;
    padding-right: 38px;
  }
}

.product-card__wrapper {
  display: flex;
  justify-content: space-between;
  max-width: 280px;
  margin: 0 auto;

  @media (min-width: $tabletWidth) {
    display: block;
  }
}

.product-card__parameters {
  @include list-normalize;
  min-width: 141px;
  margin-top: 14px;

  @media (min-width: $tabletWidth) {
    margin-top: 20px;
  }
}

.product-card__parameter {
  display: flex;
  justify-content: space-between;
  font-size: 12px;
  line-height: 16px;
  font-family: $Arial;
  color: $deepGraphite;

  @media (min-width: $tabletWidth) {
    font-size: 16px;
    line-height: 16px;
    padding-bottom: 8px;
    border-bottom: 1px solid $lighterGrey2;
    margin-top: 7px;
  }
}

.product-card__img {
  overflow: hidden;
  min-width: 140px;
  height: 112px;
  margin-left: auto;
  margin-right: auto;

  @media (min-width: $tabletWidth) {
    position: absolute;
    left: -1px;
    top: -45px;
    overflow: visible;
  }

  &--big {
    @media (min-width: $tabletWidth) {
      top: -72px;
      left: -2px;
    }
  }
}

.product-card__name {
  max-width: 76px;
  font-size: 16px;
  line-height: 20px;
  font-weight: normal;
  margin: 0;
  text-transform: uppercase;

  @media (min-width: $tabletWidth) {
    text-align: center;
    font-size: 20px;
    line-height: 24px;
    max-width: 150px;
    margin-left: auto;
    margin-right: auto;
  }
}

.product-card__order {
  margin-top: 13px;
  margin-left: auto;
  margin-right: auto;

  @media (min-width: $tabletWidth) {
    font-size: 16px;
    line-height: 20px;
    margin-top: 33px;
    max-width: 248px;
    padding-top: 9.5px;
    padding-bottom: 9.5px;
  }

  @media (min-width: $desktopWidth) {
    max-width: 168px;
  }
}
